import { WxcButton, WxcCell } from '../../index';
import Title from '../_mods/title.vue';
import Category from '../_mods/category.vue';
import { setTitle } from '../_mods/set-nav';

const modal = weex.requireModule('modal');
export default {
  components: { Title, Category, WxcButton, WxcCell },
  data: () => ({
    type: 'red',
    text: '按钮文字',
    width: '702px',
    height: '88px',
    disabled: false,
    backgroundColor: '#FF5000',
    borderColor: '#FF5000',
    borderRadius: '12px',
    fontSize: '36px',
    color: '#FFFFFF'
  }),
  computed: {
    btnStyle () {
      const { width, height, backgroundColor, borderColor, borderRadius } = this;
      const customStyle = {};

      if (width) {
        customStyle.width = width;
      }

      if (height) {
        customStyle.height = height;
        customStyle.lineHeight = height;
      }

      if (backgroundColor) {
        customStyle.backgroundColor = backgroundColor;
      }

      if (borderColor) {
        customStyle.borderColor = borderColor;
        customStyle.borderWidth = '1px';
        customStyle.borderStyle = 'solid';
      }

      if (borderRadius) {
        customStyle.borderRadius = borderRadius;
      }
      return customStyle;
    },
    textStyle () {
      const { fontSize, color } = this;
      const customStyle = {};
      if (fontSize) {
        customStyle.fontSize = fontSize;
      }
      if (color) {
        customStyle.color = color;
      }
      return {
        fontSize, color
      }
    }
  },
  created () {
    setTitle('Button');
  },
  methods: {
    wxcButtonClicked (e) {
      const { type, disabled } = e;
      modal.toast({
        message: disabled ? `disabled=${disabled}` : `type=${type}`
      })
    }
  }
}
